<template>
  <div class="ucenter">
    <div class="login-wrap" v-show="showLogin">
      <p v-show="showTishi">{{tishi}}</p>
      <input type="text" placeholder="请输入用户名" v-model="username">
      <input type="password" placeholder="请输入密码" v-model="password">
      <button>登录</button>
      <span @click="ToRegister">没有账号？马上注册</span>
    </div>

    <div class="register-wrap" v-show="showRegister">
      <p v-show="showTishi">{{tishi}}</p>
      <input type="text" placeholder="请输入用户名" v-model="newUsername">
      <input type="password" placeholder="请输入密码" v-model="newPassword">
      <button >注册</button>
      <span @click="ToLogin">已有账号？马上登录</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "ucenter",
    data() {
      return {
        showLogin: false,
        showRegister: true,
        showTishi: false,
        tishi: '',
        username: '',
        password: '',
        newUsername: '',
        newPassword: ''
      }
    },
    methods:{
      ToRegister(){
        console.log(1)
        this.showRegister = !this.showRegister;
        this.showLogin = !this.showLogin;

      },
      ToLogin(){
        console.log(2);
        this.showRegister = !this.showRegister;
        this.showLogin = !this.showLogin;
      }
    }
  }
</script>

<style scoped>
  .login-wrap,.register-wrap {
    text-align: center;
    margin-top: 50px;
  }

  input {
    display: block;
    width: 250px;
    height: 40px;
    line-height: 40px;
    margin: 0 auto;
    margin-bottom: 10px;
    outline: none;
    border: 1px solid #888;
    padding: 10px;
    box-sizing: border-box;
  }

  p {
    color: red;
  }

  button {
    display: block;
    width: 250px;
    height: 40px;
    line-height: 40px;
    margin: 0 auto;
    border: none;
    background-color: #41b883;
    color: #fff;
    font-size: 16px;
    margin-bottom: 5px;
  }

  span {
    cursor: pointer;
  }

  span:hover {
    color: #41b883;
  }
</style>



