<template>
  <div>
    <MusicList :url="hotUrl" />
  </div>
</template>

<script>
  import MusicList from '../../components/Music_List'
  export default {
    name: "hot_list",
    data(){
      return{
        hotUrl:'/v1/restserver/ting?method=baidu.ting.billboard.billList&type=2&size=6&offset=6'
      }
    },
    components:{
      MusicList
    }

  }
</script>

<style scoped>

</style>



