<template>
  <div class="more-list">
    <!--    更多:{{this.$route.params.musictype}}-->
    <!--    {{this.$route.params.musictype}}-->
    <div class="wrapper">
      <h3>{{this.$route.params.title}}</h3>
      <VuePullRefresh :on-refresh="onRefresh">
        <router-link tag="div" :to="{name:'MusicPlay',params:{songid:item.song_id}}" class="info url log" v-for="(item,index) in moreListData" :key="index">
          <div class="poster">
            <img v-lazy="item.pic_big" :alt="item.title">
            <div class="title">{{item.title}}-{{item.artist_name}}</div>
          </div>
          
        </router-link>
      </VuePullRefresh>
    </div>
  </div>
</template>

<script>
  import VuePullRefresh from 'vue-pull-refresh'

  export default {
    name: "morelist",
    data() {
      return {
        moreListData: [],
        offset:0,
      }
    },
    components: {
      VuePullRefresh
    },
    mounted() {
      var moreListUrl = this.HOST + '/v1/restserver/ting?method=baidu.ting.billboard.billList&type=' + this.$route.params.musictype + '&size=12&offset=0';
      this.$axios.get(moreListUrl)
        .then(res => {
          // console.log(res.data);
          this.moreListData = res.data.song_list;
        })
        .catch(err => {
          console.log(err, '拉取榜单失败')
        })
    },
    methods: {
      //下拉时触发的函数
      onRefresh: function () {
        var that = this;
        const moreListUrl = this.HOST + "/v1/restserver/ting?method=baidu.ting.billboard.billList&type=" + this.$route.params.musictype + "&size=12&offset=" + this.offset;
        return new Promise(function (resolve, reject) {
          setTimeout(() => {
            that.$axios.get(moreListUrl)
              .then(res => {
                // console.log(res.data);
                that.offset >= res.data.billboard.billboard_songnum - 12 ? console.log("没数据了") : that.offset += 12;
                  // that.moreListData = that.moreListData.concat(res.data.song_list)
                  that.moreListData = res.data.song_list
                resolve();
              })
              .catch(error => {
                console.log(error);
              })
          })
        });
      }
    }
  }
</script>

<style scoped>
  .wrapper {
    padding-top: 13px;
    text-align: center;
    margin-bottom: 20px;
    background: #fff;
    clear: both;
    overflow: hidden;
  }

  h3 {
    padding-bottom: 10px;
    font-size: 22px;
    text-align: left;
    margin-left: 17px;
    margin-bottom: 5px;
  }
  .wrapper .poster {
    text-align: center;
  }
  .wrapper .poster .title {
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-top: 4px;
    line-height: 14px;
    max-height: 28px;
    margin-bottom: 2px;
  }
  .wrapper .info {
    width: 42%;
    float: left;
    text-align: center;
    padding-left: 17px;
    display: block;
    text-align: left;
    margin-bottom: 10px;
    position: relative;
  }
  .wrapper .text-wrap{
     

  }
</style>



