<template>
  <div>
    <Music_List :url="kingUrl"/>
  </div>
</template>

<script>

  import Music_List from "../../components/Music_List";
  export default {
    name: "king_list",
    data(){
      return{
        kingUrl:'/v1/restserver/ting?method=baidu.ting.billboard.billList&type=21&size=6&offset=0'
      }
    },
    components: {Music_List}
  }
</script>

