<template lang="html">
  <div class="play">
    <div class="header">
      <div class="title">
        <router-link to="/">
          <i class="iconfont icon-shouye left"></i>
        </router-link>
        <div class="music-info">
          <p>{{currentUrl.songinfo.title}}</p>
          <p class="author">{{ currentUrl.songinfo.author }}</p>
        </div>
        <router-link to="/search"><i class="iconfont icon-sousuo right"></i></router-link>
      </div>
    </div>

    <div class="song-info">
      <div class="song-info-img">
        <img v-lazy="currentUrl.songinfo.pic_big">
        <LRC :durationTime="durationTime" :currentTime="currentTime" :songid="this.$route.params.songid"/>
      </div>
      <div class="iconbox">
        <i class="iconfont icon-shoucang2 left"></i>
        <i class="box"></i>
        <i class="iconfont icon-xiazai right"></i>
      </div>
    </div>
    <div class="song">
      <audio class="diyAudio" ref="player" :src="currentUrl.bitrate.show_link" controls autoplay></audio>
   
    </div>

  </div>
</template>

<script>

  import Vue from "vue"
  import "../assets/font/iconfont.css"
  // import LRC from "../components/LRC"
  // 异步操作
  const LRC = Vue.component("lrc", (resolve) => require(["../components/LRC"], resolve))

  export default {
    name: "musicplay",
    data() {
      return {
        currentUrl: {
          songinfo: {
            title: '',
            author: ""
          },
          bitrate: {
            show_link: ""
          }
        },
        currentTime: 0,
        durationTime: 0
      }
    },
    components: {
      LRC
    },
    mounted() {
      const playUrl = this.HOST + "/v1/restserver/ting?method=baidu.ting.song.play&songid=" + this.$route.params.songid;
      this.$axios.get(playUrl)
        .then(res => {
          this.currentUrl = res.data;
        })
        .catch(error => {
          console.log(error);
        })
      this.addEventListeners();
    },
    beforeDestroyed() {
      this.removeEventListeners();
    },
    methods: {//监听当前播放时间和当前歌曲的总时间
      addEventListeners() {
        this.$refs.player.addEventListener('timeupdate', this._currentTime),
          this.$refs.player.addEventListener('canplay', this._durationTime)
      },
      removeEventListeners: function () {
        this.$refs.player.removeEventListener('timeupdate', this._currentTime)
        this.$refs.player.removeEventListener('canplay', this._durationTime)
      },
      _currentTime() {
        this.currentTime = this.$refs.player.currentTime
        // currentTime是audio标签提供的获取当前播放时间的方法
      },
      _durationTime() {
        this.durationTime = this.$refs.player.duration
        // duration是audio标签提供的获得歌曲播放整体时间的方法
      }
    }

  }
</script>

<style scoped>


  .header {
    padding: 15px;
  }

  .music-info {
    flex: 1;
    font-size: 20px;
  }

  .title {
    display: flex;
    text-align: center;
  }

  .left {
    font-size: 30px;
  }

  .ca {
    color: red;
  }

  .right {
    font-size: 30px;
  }

  .song-info {
    padding: 15px;
  }

  .song-info-img {
    text-align: center;
  }
@media screen and (max-width: 767px){
  .song-info-img img {
    width: 60%;
    border-radius: 5px;
    box-shadow: 0 0 10px 0 rgba(50, 50, 50, .31);
  }
  .song {
    width: 100%;
    text-align: center;
    position: fixed;
    height:54px;
    bottom:0;
    background:rgba(169, 169, 169, .31);
  }
}
@media screen and (min-width:960px) {
  .song-info-img img {
    width: 80%;
    border-radius: 5px;
    box-shadow: 0 0 10px 0 rgba(50, 50, 50, .31);
  }
  .song {
    width: 100%;
    height:50px;
    text-align: center;
    bottom:0;
  }
}
  .song audio {
    width: 100%;
  }


  .iconbox {
    display: flex;
    margin-top: 30px;
  }

  .iconbox .box {
    flex: 1;
  }

 

  .active {
    color: #222;
  }

  .author {
    font-size: 12px;
    color: #999;
  }

</style>



