<template lang="html">
  <div class="art-list">
    <ul class="list">
      <router-link tag="li" :to="{name:'MusicPlay',params:{songid:item.song_id}}" class="song" v-for="(item,index) in listArr" :key="index">
        <div class="left">
          {{ item.title }}
        </div>
      </router-link>
      <button @click="addlist(ting_uid)" class="btn" type="button">加载更多>></button>
    </ul>

  </div>
</template>

<script>
  export default {
    name:"artlist",
    data(){
      return{
        listArr:[],
        limits:10
      }
    },
    props:{
      ting_uid:{
        type:String,
        default:"0"
      }
    },
    mounted(){
      const ArtList = this.HOST + "/v1/restserver/ting?method=baidu.ting.artist.getSongList&tinguid="+ this.ting_uid +"&limits="+this.limits+"&use_cluster=1&order=2";
      this.$axios.get(ArtList)
        .then(res => {
          this.listArr = res.data.songlist
        })
        .catch(error => {
          console.log(error);
        })
    },
    methods:{
      addlist(ting_uid){
        var that = this;
        return new Promise(function (resolve, reject) {
          setTimeout(() => {
            var ArtListUrl = that.HOST + "/v1/restserver/ting?method=baidu.ting.artist.getSongList&tinguid="+ that.ting_uid +"&limits="+that.limits+"&use_cluster=1&order=2";
            that.$axios.get(ArtListUrl)
              .then(res => {
                that.limits > res.data.songlist.songnums-10 ? console.log('没有更多歌曲了') : that.limits += 10;
                that.listArr = that.listArr.concat(res.data.songlist);
                console.log(that.listArr);
                resolve();
              })
              .catch(error => {
                console.log(error);
              })
          })

        })
    }
  }

  }
</script>

<style scoped>

  .art-list{
    padding: 0 17px;
  }

  .song{
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #999;
  }
  .left{
    font-size: 18px;
  }
  .btn{
    height: 30px;
    width: 100%;
    line-height: 30px;
    font-size: 16px;
    color: #222;
    text-align: center;
    background: #299DE7;
  }


</style>



