<template>
  <div>
    <Music_List :url="newsUrl"/>

  </div>
</template>

<script>
  import Music_List from "../../components/Music_List";

  export default {
    name: "news_list",
    components: {
      Music_List
    },
    data(){
      return{
        newsUrl:'/v1/restserver/ting?method=baidu.ting.billboard.billList&type=1&size=6&offset=3'
      }
    }
  }
</script>

<style scoped>

</style>



